/* eslint-disable no-undef */
document.onreadystatechange = () => {
	const spinner = document.getElementById("spinner");
	const splash = document.querySelector(".splash");
	if (document.readyState === "interactive") {
		setTimeout(() => {
			spinner.setAttribute("hidden", false);
		}, 3000);
	} else if (document.readyState === "complete") {
		splash.remove();
	} else {
		spinner.setAttribute("hidden", true);
	}
};
document.addEventListener("DOMContentLoaded", () => {
	const favorite = document.querySelector("#favorite");
	const like = document.querySelector("#like");
	const list = document.querySelectorAll(".nav-bar li");
	const request = window.indexedDB.open("ZEED_RESTO", 1);

	const params = new Proxy(new URLSearchParams(window.location.search), {
		get: (searchParams, prop) => searchParams.get(prop),
	});

	request.onsuccess = (ev) => {
		const db = ev.target.result;
		const transaction = db.transaction(["Favorite","Liked"], "readonly");
		const Favorite = transaction.objectStore("Favorite");
		const Liked = transaction.objectStore("Liked");
		Favorite.openCursor().onsuccess = (ev) => {
			const cursor = ev.target.result;
			if (cursor !== undefined && cursor !== null) {
				const el = cursor.value;
				if (el.id === params.id) {
					favorite.classList.add("active");
					favorite.innerText = "Favorited";
				}
				if (cursor) {
					cursor.continue();
				}
			}
		};
		Liked.openCursor().onsuccess = (ev) => {
			const cursor = ev.target.result;
			if (cursor !== undefined && cursor !== null) {
				const el = cursor.value;
				if (el.id === params.id) {
					like.classList.add("active");
					like.innerText = "Liked";
				}
				if (cursor) {
					cursor.continue();
				}
			}
		};
	};

	document.querySelector("x-menu").addEventListener("click", () => {
		document.querySelector(".nav-bar").classList.toggle("active");
	});

	list.forEach((element) => {
		element.addEventListener("click", () => {
			document.querySelector(".nav-bar").classList.toggle("active");
			document.querySelector(".nav-bar li a").classList.toggle("active");
		});
	});
});

document.addEventListener("scroll", () => {
	const header = document.querySelector("header");
	if (window.scrollY > 10) {
		header.classList.toggle("scroll");
	}
});

if ("serviceWorker" in navigator) {
	window.addEventListener("load", () => {
		navigator.serviceWorker.register("./sw.bundle.js");
	});
}
